<template>
    <p-card>
        <template #content>
            <div
                style="display: flex; justify-content: space-between; margin-bottom: 5px; "
            >
                <div>
                    <span class="p-mr-2"><strong>Sales -</strong></span>
                    <span class="info-text">
                        {{ displayPatient.chart ? displayPatient.chart : " " }}
                        {{
                            displayPatient.firstName
                                ? displayPatient.firstName
                                : " "
                        }}
                        {{
                            displayPatient.lastName
                                ? displayPatient.lastName
                                : " "
                        }}
                    </span>
                </div>

                <div class="p-field p-grid">
                    <label for="searchPatient"
                        ><strong>Search Patient</strong></label
                    >
                    <div class="p-col">
                        <p-auto-complete
                            v-model="form.patient"
                            :suggestions="patientOpts"
                            @complete="patientSuggestions"
                            field="label"
                            optionLabel="label"
                        />
                    </div>
                </div>
            </div>
        </template>
    </p-card>
    <p-data-table :value="invoiceItems" :loading="loading" style="margin-top: 5px;">
        <p-column field="trandate" header="Date" />
        <p-column field="recordtype" header="Type" />
        <p-column field="typebaseddocumentnumber" header="Document Number">
            <template #body="{data}">
                <label
                    @click="openInvDetails(data.id)"
                    style="cursor: pointer; color: blue;"
                    >{{ data.typebaseddocumentnumber }}</label
                >
            </template>
        </p-column>
        <p-column field="custbody_stc_amount_after_discount" header="Amount" />
        <p-column field="foreignamountunpaid" header="Amount Due" />
        <!-- <p-column field="item" header="Item" /> -->
        <p-column field="billingstatus" header="Status">
            <template #body="{data}">
                <label>{{
                    Number(data.foreignamountpaid) ===
                    Number(data.custbody_stc_amount_after_discount)
                        ? "Paid In Full"
                        : Number(data.foreignamountunpaid) !== 0 &&
                          Number(data.foreignamountpaid) !== 0
                        ? "Partially Paid"
                        : "Pending"
                }}</label>
            </template>
        </p-column>
        <!-- <p-column field="custbody_nlfc_po_cust_field_5" header="Location" /> -->
        <p-column field="employee" header="Sales Rep" />
        <p-column field="createdby" header="Created By" />

        <p-column field="createddate" header="Created Date" />
        <p-column field="lastmodifiedby" header="Modified By" />
        <p-column field="lastmodifieddate" header="Modified Date" />

        <!-- <p-column field="payingTransaction" header="Paying Transaction" />
        <p-column field="paymentMethod" header="Payment Method" />
        <p-column field="billing" header="Billing" />
        <p-column field="poCheckNo" header="Po/check Number" />
        <p-column field="memo" header="Memo" /> -->
    </p-data-table>
    <p-dialog
        v-model:visible="invDetailsDialog"
        modal
        :header="'Invoice Details of - ' + (selectedInvId || '')"
        :style="{ width: '80rem' }"
    >
        <div
            v-if="selectedInvoice"
            style="padding: 20px; font-family: Arial, sans-serif;"
        >
            <div style="text-align: center; margin-bottom: 20px;">
                <h2 style="margin: 0; color: #2c3e50;">
                    Newlife Fertility Centre
                </h2>
            </div>

            <hr />
            <div
                style="
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 15px;
                "
            >
                <div>
                    <p>
                        <strong>Invoice No:</strong>
                        {{ selectedInvoice.invoice?.tranId || "" }}
                    </p>
                    <p>
                        <strong>Invoice Date:</strong>
                        {{ selectedInvoice.invoice?.tranDate || "" }}
                    </p>
                    <p>
                        <strong>Account RefName:</strong>
                        {{ selectedInvoice.invoice?.account?.refName || "" }}
                    </p>
                    <p>
                        <strong>Billing Address:</strong>
                        {{ selectedInvoice.invoice?.billAddress || "" }}
                    </p>
                </div>

                <div>
                    <p>
                        <strong>Customer ID:</strong>
                        {{ selectedInvoice.invoice?.custbody2?.id || "" }}
                    </p>
                    <p>
                        <strong>Customer Name:</strong>
                        {{ selectedInvoice.invoice?.custbody2?.refName || "" }}
                    </p>
                    <p>
                        <strong>Cycle Patient:</strong>
                        {{ selectedInvoice.invoice?.entity?.refName || "" }}
                    </p>
                    <p>
                        <strong>Patient Email:</strong>
                        {{ selectedInvoice.invoice?.email || "" }}
                    </p>
                    <p>
                        <strong>Sales Rep:</strong>
                        {{ selectedInvoice.invoice?.salesRep?.refName || "" }}
                    </p>
                </div>
            </div>

            <h3 style="margin-top: 20px; color: #34495e;">Invoice Items</h3>
            <p-data-table
                :value="selectedInvoice.invoice?.item?.items || []"
                style="margin-top: 10px;"
            >
                <p-column field="line" header="#" />
                <p-column field="description" header="Description" />
                <p-column field="quantity" header="Qty" />
                <p-column field="rate" header="Rate" />
                <p-column field="amount" header="Amount" />
                <p-column field="class.refName" header="Service Type">
                    <template #body="{ data }">
                        {{ data.class?.refName || "N/A" }}
                    </template>
                </p-column>
            </p-data-table>

            <div style="display: flex; justify-content: space-between; margin-top: 15px;">
                <div>
                    <p>
                        <strong>Paid Amount:</strong>
                        {{ selectedInvoice.invoice?.amountPaid || "0" }}
                    </p>
                    <p>
                        <strong>Remaining Amount:</strong>
                        {{ selectedInvoice.invoice?.amountRemaining || "0" }}
                    </p>
                </div>
                <div style="text-align: right;">
                    <p>
                        <strong>Subtotal:</strong>
                        {{ selectedInvoice.invoice?.subtotal || "0" }}
                    </p>
                    <p>
                        <strong>Tax:</strong>
                        {{
                            selectedInvoice.invoice
                                ?.custbody_stc_tax_after_discount || "0"
                        }}
                    </p>
                    <h3 style="margin: 5px 0;">
                        Amount:
                        {{ selectedInvoice.invoice?.total || "0" }}
                    </h3>
                </div>
            </div>

            <hr />
            <p style="margin: 5px 0; font-size: 24px;">
                Status:
                {{ selectedInvoice.invoice?.status?.refName || "" }}
            </p>

            <!-- Buttons -->
            <div style="text-align: right; margin-top: 15px;">
                <p-button
                    label="Close"
                    class="p-button-secondary p-mr-2"
                    @click="cancelInvDialog"
                />
                <!-- <p-button
                    label="Print"
                    class="p-button-success"
                    icon="pi pi-print"
                /> -->
            </div>
        </div>

        <div v-else style="text-align: center; padding: 20px;">
            <p>Loading invoice details...</p>
        </div>
    </p-dialog>
</template>

<script>
import { useStore } from "vuex";
import { computed, ref, watch } from "vue";
import { $axios } from "@/service/app-http";
import Service from "@/service/art-cycle.js";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const store = useStore();
        const patient = computed(() => store.state.artCycle.patient);
        const invDetailsDialog = ref(false);
        const selectedInvoice = ref(null);
        const selectedInvId = ref(null);
        const form = ref({ patient: null });
        const patientOpts = ref([]);
        const invoiceItems = ref([]);
        const loading = ref(false);
        const patientSuggestions = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: "F".toLowerCase()
            }).then((resp) => {
                patientOpts.value = resp.data;
            });
        };

        const displayPatient = computed(() => {
            if (form.value.patient) {
                const selected = form.value.patient;
                return {
                    chart: selected.demographicNo,
                    firstName: selected.firstName,
                    lastName: selected.lastName
                };
            } else if (patient.value) {
                const p = patient.value;
                return {
                    chart: p.chart || "",
                    firstName: p.firstName || "",
                    lastName: p.lastName || ""
                };
            } else {
                return {};
            }
        });

        const openInvDetails = (invId) => {
            selectedInvId.value = invId;
            invDetailsDialog.value = true;
            selectedInvoice.value = null;
            $axios
                .get(`/ns/invoice-details/${invId}`)
                .then((res) => {
                    // console.log("Invoice Response:", res.data);
                    selectedInvoice.value = res.data;
                })
                .catch(() => {})
                .finally(() => {});
        };

        const cancelInvDialog = () => {
            invDetailsDialog.value = false;
        };

        watch(
            () => displayPatient.value.chart,
            (newChart) => {
                if (newChart) {
                    loading.value = true;
                    $axios
                        .get(`/ns/patient-invoices/${newChart}`)
                        .then((res) => {
                            // console.log("Invoice Response:", res.data);
                            invoiceItems.value = res.data.invoices.items || [];
                        })
                        .catch(() => {
                            invoiceItems.value = [];
                        })
                        .finally(() => {
                            loading.value = false;
                        });
                }
            },
            { immediate: true }
        );

        return {
            form,
            patientOpts,
            patientSuggestions,
            displayPatient,
            invoiceItems,
            openInvDetails,
            invDetailsDialog,
            cancelInvDialog,
            selectedInvId,
            selectedInvoice,
            loading
        };
    }
};
</script>
